import { useQuery } from "@tanstack/react-query";
import { motion } from "framer-motion";
import styled from "styled-components";
import {
  getAiringTodayTv,
  getOnTheAirTv,
  getPopularTv,
  getTopRateTv,
  IGetMoviesResult,
  IMovie,
} from "../api";

import Banner from "../Components/Banner";
import Slider from "../Components/Slider";

const Wrapper = styled.div`
  background: #000;
  padding-bottom: 200px;
  // 화면 x 좌표(좌우)가 늘어나면서 생기는 스크롤바를 숨겨줌
  /* overflow-x: hidden; */
`;

const Loader = styled.div`
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const Logo = styled(motion.svg)`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 500px;
  height: 150px;
  fill: ${(props) => props.theme.red};
`;

const SliderArea = styled.div`
  position: relative;
  margin-top: 130px;
`;

function Tv() {
  const { data: tv_popular, isLoading } = useQuery<IGetMoviesResult>(
    ["tv", "popular"],
    getPopularTv
  );

  const { data: tv_topRate } = useQuery<IGetMoviesResult>(
    ["tv", "topRate"],
    getTopRateTv
  );

  const { data: onTheAir } = useQuery<IGetMoviesResult>(
    ["tv", "OnTheAir"],
    getOnTheAirTv
  );

  const { data: airingToday } = useQuery<IGetMoviesResult>(
    ["tv", "AiringToday"],
    getAiringTodayTv
  );

  return (
    <Wrapper>
      {isLoading ? (
        <Loader>
          <Logo
            xmlns="http://www.w3.org/2000/svg"
            width="1024"
            height="276.742"
            viewBox="0 0 1024 276.742"
          >
            <motion.path
              d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z"
              fill="#d81f26"
            />
          </Logo>
          Loading...
        </Loader>
      ) : (
        <>
          <Banner
            banner={tv_popular?.results[0] as IMovie}
            detailSearchUrl={"tv/banner"}
            requestUrl={"tv"}
          />
          <SliderArea>
            <Slider
              data={tv_popular as IGetMoviesResult}
              title={"가장 인기있는 영화"}
              menuName={"tv"}
              mediaType={"tv"}
              listType={"popular"}
            ></Slider>

            <Slider
              data={tv_topRate as IGetMoviesResult}
              title={"별점이 높은 영화"}
              menuName={"tv"}
              mediaType={"tv"}
              listType={"top_rate"}
            ></Slider>

            <Slider
              data={onTheAir as IGetMoviesResult}
              title={"On The Air"}
              menuName={"tv"}
              mediaType={"tv"}
              listType={"on_the_air"}
            ></Slider>

            <Slider
              data={airingToday as IGetMoviesResult}
              title={"Airing Today"}
              menuName={"tv"}
              mediaType={"tv"}
              listType={"airing_today"}
            ></Slider>
          </SliderArea>
        </>
      )}
    </Wrapper>
  );
}

export default Tv;
